<script>
import Courseifon from "@/views/teachers/Courseifon";
import Timetable from "@/views/teachers/Timetable";
export default {
  data() {
    return {
      yearTerms: [],
      courses: [],
      formInline: {
        user: ""
      },
      options: [],
      value: "",
      pointList: [],
      isavtive: 0,
      userIdentity: "",
      versionList: [],
      versionId: 0,
      roleName: ''
    };
  },
  components: {
    Courseifon,
    Timetable
  },
  methods: {
    getClaim(id) {
      this.versionId = id;
      this.pointList = [
        {
          id
        }
      ]
    },
    changeclass(index) {
      let res = [
        {
          id: "12",
