        }
      ];
      this.isavtive = index;
      this.pointList = res;
    },
    // /info/information/getClassByTeacher
    getClassByTeacher() {
      this.$get("/info/information/getClassByTeacher").then(res => {
        this.options = res;
      });
    },
    getInfo() {
      if(localStorage.getItem('versionId')) {
        this.getClaim(localStorage.getItem('versionId'))
      } else {
        setTimeout(() => {
          this.getInfo()
        }, 200); 
      }
    }
