<style lang="scss" scoped>
.Curriculum1 {
  // margin-bottom: 100px;
  .requirement-li {
    padding-bottom: 12px;
    ul {
      overflow: hidden;
      li {
        list-style: none;
        float: left;
        padding: 8px 20px;
        background-color: #ccc;
        margin-right: 6px;
        cursor: pointer;
        &.active {
          background-color: rgb(50, 104, 247);
          color: #fff;
        }
      }
    }
  }
  .Curriculum-dv {
