    height: 60px;
    line-height: 60px;
    // background-color: #fff;
    text-align: center;
  }
  .Curriculum-dv0 {
    padding-top: 5px;
    background-color: #fff;
    // width: 64vw;
    width: 100%;
    position: relative;
  }
  .Curriculum-form {
    position: absolute;
    top: 14px;
    left: 200px;
  }
  .Curriculum-dv2 {
    margin-top: 6px;
    padding: 0 40px;
    box-sizing: border-box;
    overflow: hidden;
    background-color: #fff;
    margin-bottom: 110px;
  }
  .Curriculum-dv1 {
    text-align: left;
    margin-left: 40px;
    padding: 20px 0;
    background-color: #fff;
    font-weight: 700;
  }
  table.gridtable {
    font-size: 14px;
    font-family: verdana, arial, sans-serif;
    font-size: 11px;
    color: #333333;
    border-width: 1px;
    border-color: #666666;
    border-collapse: collapse;
  }
  table.gridtable th {
    border-width: 1px;
    padding: 12px;
    border-style: solid;
    border-color: #666666;
    background-color: #bfe1fd;
  }
  table.gridtable td {
    border-width: 1px;
    padding: 12px;
    border-style: solid;
    border-color: #666666;
    background-color: #ffffff;
    font-size: 14px;
  }
  table.gridtable1 {
    font-size: 14px;
    font-family: verdana, arial, sans-serif;
    font-size: 11px;
    color: #333333;
    border-width: 1px;
    border-color: #666666;
    border-collapse: collapse;
  }
  table.gridtable1 th {
    border-width: 1px;
    padding: 12px;
    border-style: solid;
    border-color: #666666;
    background-color: #bfe1fd;
  }
  table.gridtable1 td {
    border-width: 1px;
    padding: 12px;
    border-color: #666666;
    background-color: #ffffff;
    font-size: 14px;
  }
  .theads {
    width: 100%;
    border-style: solid;
    border-color: #666666;
    padding: 8px;
    border-width: 1px;
  }
}
</style>



