<template>
 <div class="center2">
    <div class="center2_content" ref="open">
         <!-- <div class="table-container">
            <ul class="table-ul1">
                <li class="table-ul1_li" v-for="(vo,index) in tableData" :key="index" :style="{width:oftenWidth}">
                    <p>{{vo.year}}</p>
                    <ul>
                        <li v-for="(item,num) in vo.list" :key="`${index}-${num}`" @click="courseInfo(item.root_course_id)"><span>{{item.name}}</span><i ref="line"></i></li>
                    </ul>
                </li>
            </ul>
        </div> -->
        <img src="@/assets/graduated.png" alt="" style="width:76%; height:100%;">
    </div>
 </div>
</template>
<script>
import studentApi from '@/api/studentApi'
export default {
 name: 'ResultQuery',
 components: {},
 data() {
    return {
        pageTitle:'成绩查询',
        currentPage:1,
        tableData: [],
        list:{
            courses: [
            ],
            yearTerms: [
            
            ]
        },
        yearTerms:[],
        courses:[]
    }
 },
 created(){},
mounted(){
    //  this.initData()
    //  this.initFun()
 },
computed:{
    oftenWidth(){
        return `${(1 / this.tableData.length) * 100 - (0.2 * (this.tableData.length + 1))}%`
    }
},
methods:{
    initFun(){
        const courses=this.courses;
        const yearTerms=this.yearTerms;
        const lengthArr=[];
        yearTerms.forEach(item =>{
            item.list=[]
            courses.forEach(vo =>{
                if(vo.order === item.order){
                     item.list.push(vo)
                }
            })
            lengthArr.push(item.list.length)
        })
       const maxLength=Math.max.apply(null, lengthArr);
       yearTerms.forEach(item =>{
           const lengths=item.list.length
           if(lengths < maxLength){
               const leng= maxLength - lengths
                for(let i=0; i<leng; i++ ){
                         item.list.push({
                            courseName:'',
                            courseId:''
                        })
                }
           }
       })
        this.tableData=yearTerms;
        let tmpArr = []
        this.tableData.forEach(item => {
          tmpArr.push(item.list.length)
        })
        this.tableHeight=`${Math.max(...tmpArr) * 47 + 100}px`
    },
    initData(){
        const url =studentApi.getAllCourse;
        this.$get('/info/information/getOrderCourse').then(res =>{
            // let arr = []
            // res.courses.forEach((item,index)=>{
            //     arr.push(item.name)
            //     console.log(arr,'arr')
            // })
        if(res.courses.length == 0 || res.yearTerms.length == 0){
                this.$refs.open.innerHTML= '暂无数据'
        }else{
            this.yearTerms = res.yearTerms
            this.courses = res.courses                 
            this.initFun()
        } 
        })
        // this.$refs.line.oftenWidth
    },
    headInfo(item){
        this.pageTitle=item[1]
    },
    courseInfo(item){
        this.$router.push({path: '/studentside/graduation',query: {id: item}})
    },
}
}
</script>

<style lang='scss'>
//   @import '../../styles/center.scss';
  .center2{
      .center2_content{
          padding: 0;
          height: 100%;
      }
      .table-ul1{
      width:100%;
      height:100%;
      clear:both;
      overflow: -webkit-paged-x;
      background:#fff;
      li{
          list-style-type:none;
          height:46px;
          text-align:center;
          line-height:46px;         
          border-right:1px solid #EBEEF5;
          border-bottom:1px solid #EBEEF5;
      }
      .table-ul1_li{
         float:left;
         position:relative;
         border-top:1px solid #EBEEF5;
         p{
           width:100%;
           height:100%;
           text-align:center;
           vertical-align:center;
           background:#F8FAFF;
           font-size:12px;
           color:#666;
           font-weight:700;
         }
         ul{
             width:100%;
             position:absolute;
             left:0;
             top:46px;
             li{
                 width:100%;
                 height:46px;
                 font-size:14px;
                 color:#606266;
                 cursor: pointer;
             }
             li:nth-of-type(even){
                 background:rgba(250,251,255,.2);
             }
         }
      }
     
      .table-ul1_li:first-child{
           border-left:1px solid #EBEEF5;
           ul{
               li{
                    border-left:1px dashed #EBEEF5;
                    
               }
           }
      }
  }
