
<template>
  <div class="center" style="margin-bottom:0;">
    <div class="center_content1">
      <div class="table-container">
        <!-- 学生端 -->
        <ul class="table-ul" v-if="roleName === '学生'" :style="{height:tableHeights}" >
          <li
            class="table-ul_li1"
            v-for="(vo,index) in tableData"
            :key="index"
            :style="{width:oftenWidth}"
          >
            <p class="table-ul-p">{{vo.year}}</p>
            <ul>
              <li 
                v-for="(item,num) in vo.list"
                :key="num"
                @click="courseInfos(item)"
                >
                <p style="overflow: hidden; text-overflow:ellipsis; white-space: nowrap;">{{item.name}}</p>
              </li>
            </ul>
          </li>
        </ul>
        <!-- 教师端 -->
        <ul class="table-ul" v-else-if="roleName === '课程负责人' || roleName === '教师'" :style="{height:tableHeights}" >
          <li
            class="table-ul_li1"
            v-for="(vo,index) in tableData"
            :key="index"
            :style="{width:oftenWidth}"
          >
            <p class="table-ul-p">{{vo.year}}</p>
            <ul>
              <li
                v-for="(item,num) in vo.list"
                :key="num"
                @click="courseInfos(item)"
                >
                <p style="overflow: hidden; text-overflow:ellipsis; white-space: nowrap;">{{item.name}}</p>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import studentApi from "@/api/studentApi";
export default {
  name: "ResultQuery",
  components: {},
  props: ['pointLists'],
  data() {
    return {
      pageTitle: "排课信息",
      tableData: [],
      tableHeight: "47px",
      tableHeights: "47px",
      versionId: "",
      userIdentity: '',
      teacherData: [],
      roleName: ''
    };
  },
  created() {
    this.roleName = localStorage.getItem('roleName')
  },
  mounted() {
    if(localStorage.getItem('roleName') === '课程负责人' || localStorage.getItem('roleName') === '教师') {
      
    } else if(localStorage.getItem('roleName') === '学生') {
      this.tearcherList()
    }
    // if (this.userIdentity == "teacher") {

    // } else {
    //   this.tearcherList();
    // }
  },
  computed: {
    oftenWidth() {
      return `${(1 / this.tableData.length) * 100}%`;
    },
  },
  methods: {
       dataProcessing(res){
        const courses=res.courses;
        const yearTerms=res.yearTerms;
        const lengthArr=[];
        yearTerms.forEach(item =>{
            item.list=[]
            courses.forEach(vo =>{
                if(vo.term === item.order){
                     item.list.push(vo)
                }
            })
            lengthArr.push(item.list.length)

        })
       const maxLength=Math.max.apply(null, lengthArr);
       yearTerms.forEach(item =>{
           const lengths=item.list.length
           if(lengths < maxLength){
               const leng= maxLength - lengths
                for(let i=0; i<leng; i++ ){
                         item.list.push({
                            courseName:'',
                            courseId:''
                        })
                }
           }

       })

        this.tableData=yearTerms;
        let tmpArr = []
        this.tableData.forEach(item => {
          tmpArr.push(item.list.length)
        })
        this.tableHeight=`${Math.max(...tmpArr) * 47 + 100}px`
    },
    courseInfos(item) {
      if(item.id) {
        this.$router.push({path: '/studentside/graduation',query: {id: item.id, title:'课程体系'}})
      }
      
    },
    tearcherList() {
      if(this.roleName === '课程负责人' || this.roleName === '教师'){
        this.$get('/info/teacherinfo/getAllCourseByVersionId', {versionId: this.versionId}).then(res=>{
            res.yearTerms = [
              {
                year: "第一学期", order: 1
              },
              {
                year: "第二学期", order: 2
              },
              {
                year: "第三学期", order: 3
              },
              {
                year: "第四学期", order: 4
              },
              {
                year: "第五学期", order: 5
              },
              {
                year: "第六学期", order: 6
              },
              {
                year: "第七学期", order: 7
              },
              {
                year: "第八学期", order: 8
              }
            ]
            // this.teacherData = res
            this.dataProcessing(res)
            this.$nextTick(() => {
              let tmp = []
              document.querySelectorAll('.table-ul_li1>ul').forEach(item => {
                tmp.push(item.children.length)
              })
              this.tableHeights = `${(Math.max(...tmp) + 1) * 40 + 100}px`;
            })
          }).catch(err => {
            this.notify({
              title: 'error',
              message: '系统繁忙，请刷新重试!',
              type: 'err',
              duration: '1500',
            });
            
          })
        }else if(this.roleName === '学生'){
          this.versionId = 0
          this.$get('/info/teacherinfo/getAllCourseByVersionId', {versionId: 0}).then(res=>{
            res.yearTerms = [
              {
                year: "第一学期", order: 1
              },
              {
                year: "第二学期", order: 2
              },
              {
                year: "第三学期", order: 3
              },
              {
                year: "第四学期", order: 4
              },
              {
                year: "第五学期", order: 5
              },
              {
                year: "第六学期", order: 6
              },
              {
                year: "第七学期", order: 7
              },
              {
                year: "第八学期", order: 8
              }
            ]
            // this.teacherData = res
             this.dataProcessing(res)
            this.$nextTick(() => {
              let tmp = []
              document.querySelectorAll('.table-ul_li1>ul').forEach(item => {
                tmp.push(item.children.length)
              })
              this.tableHeights = `${(Math.max(...tmp) + 1) * 40 + 100}px`;
            })
          }).catch(err => {
            this.notify({
              title: 'error',
              message: '系统繁忙，请刷新重试!',
              type: 'err',
              duration: '1500',
            });
            
          })
        }
      }
      
    
  },
  watch: {
    pointLists(val) {
      val.forEach(item => {
        this.versionId = item.id
      })
      // if (this.userIdentity == "teacher") {
        this.tearcherList();
      // }else{
      //   this.tearcherList();
      // }
    },
    
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.center {
  .center_content1 {
    padding: 0;
  }
}

