.table-ul {
  width: 100%;
  clear: both;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  li {
    list-style-type: none;
    height: 46px !important;
    text-align: center;
    line-height: 46px;
    border-right: 0 !important;
    border-bottom: 0 !important;
    box-sizing: border-box;
  }
  .table-ul_li1 {
    float: left;
    position: relative;
    border: 0 !important;
    >p {
      width: 100%;
      height: 100%;
      text-align: center;
      vertical-align: center;
      background: #e5f7f3 !important;
      font-size: 12px;
      color: #323232;
      font-weight: 700;
    }
    ul {
      width: 100%;
      position: absolute;
      left: 0;
      top: 46px;
      li {
        width: 100%;
        height: 46px;
        font-size: 14px;
        color: #606266;
        cursor: pointer;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        border: 1px solid transparent !important;
        padding: 0 10px;
        &:hover {
          border-color: #03B187 !important;
          color: #03B187 !important;
          box-shadow:0px 2px 10px 0px rgba(3,177,135,0.2);
          border-radius: 5px;
        }
      }
      li:nth-of-type(even) {
        background: #F8F8F8 !important;
      }
    }
  }
  .table-ul_li {
    // border-left: 1px solid #ebeef5;
    ul {
      li {
        // border-left: 1px solid #ebeef5;
      }
    }
  }
